import React from 'react'
import Layout from '../components/Layout'

export default function blogs() {
    return (
        <>
            <Layout>
                <div className='container'>
                    <h1 className="yellow">Most Recent Blogs</h1>
                    <div className='child'>
                        <a href='/posts/blog1'>Blog Post 1</a>
                    </div>
                </div>
            </Layout>
        </>
    )
}
